    methods:{
      updateData(){
        var setting1=this.eventLocation
        var setting2=this.energy
        var setting3=this.numAtt
        var setting4=this.duration
        //var addition
        var factor
        if (setting2 == 'Low'){
            factor = 1
        }
        if (setting2 == 'Moderate'){
            factor = 1.5
        }
        if (setting2 == 'High'){
            factor = 2
        }
        this.water_used[0] = this.water_used[0] + setting3 * setting4 * factor
        //this.events[0].demand=(this.events[0].demand+addition)*factor
        //this.events[1].demand=(this.events[0].demand+addition)*factor
        //this.events[2].demand=(this.events[0].demand+addition)*factor
        }
